import { useCallback, useEffect, useRef, useState } from 'react'
import { FiChevronLeft, FiClock, FiExternalLink } from 'react-icons/fi'
import { t } from '../i18n'
import { VideoViewListResponse, VideoViewResponse } from '../tools/client'
import { callAPI } from '../tools/clientBridge'
import './BrowsingHistoryPopup.css'

interface BrowsingHistoryPopupProps {
  onBack: () => void
  onNavigateCollection: (videoViewId: number, videoTitle: string) => void
}

// Helper function to format relative time
function formatRelativeTime(dateStr: string): string {
  const now = new Date()
  // API returns UTC time, ensure it's parsed as UTC
  const utcDateStr = dateStr.endsWith('Z') ? dateStr : dateStr + 'Z'
  const date = new Date(utcDateStr)
  const diffMs = now.getTime() - date.getTime()
  const diffMins = Math.floor(diffMs / (1000 * 60))
  const diffHours = Math.floor(diffMs / (1000 * 60 * 60))
  const diffDays = Math.floor(diffMs / (1000 * 60 * 60 * 24))

  if (diffMins < 1) {
    return t('history_justNow')
  } else if (diffMins < 60) {
    return t('history_minutesAgo', diffMins.toString())
  } else if (diffHours < 24) {
    return t('history_hoursAgo', diffHours.toString())
  } else {
    return t('history_daysAgo', diffDays.toString())
  }
}

// Helper function to get YouTube thumbnail URL
function getYouTubeThumbnailUrl(videoId: string): string {
  return `https://img.youtube.com/vi/${videoId}/mqdefault.jpg`
}

const PAGE_SIZE = 20

export function BrowsingHistoryPopup({ onBack, onNavigateCollection }: BrowsingHistoryPopupProps) {
  const [videoViews, setVideoViews] = useState<VideoViewResponse[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [isLoadingMore, setIsLoadingMore] = useState(false)
  const [hasMore, setHasMore] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const contentRef = useRef<HTMLDivElement>(null)

  // Load initial data
  useEffect(() => {
    const loadVideoViews = async () => {
      try {
        setIsLoading(true)
        setError(null)
        const response = await callAPI<VideoViewListResponse>('listVideoViews', {
          skip: 0,
          limit: PAGE_SIZE,
        })
        setVideoViews(response.items)
        setHasMore(response.items.length < response.total)
      } catch (err) {
        console.error('Failed to load video views:', err)
        setError(err instanceof Error ? err.message : 'Failed to load history')
      } finally {
        setIsLoading(false)
      }
    }

    loadVideoViews()
  }, [])

  // Load more data
  const loadMore = useCallback(async () => {
    if (isLoadingMore || !hasMore) return

    try {
      setIsLoadingMore(true)
      const response = await callAPI<VideoViewListResponse>('listVideoViews', {
        skip: videoViews.length,
        limit: PAGE_SIZE,
      })
      setVideoViews((prev) => [...prev, ...response.items])
      setHasMore(videoViews.length + response.items.length < response.total)
    } catch (err) {
      console.error('Failed to load more video views:', err)
    } finally {
      setIsLoadingMore(false)
    }
  }, [isLoadingMore, hasMore, videoViews.length])

  // Scroll event handler for infinite scroll
  useEffect(() => {
    const contentElement = contentRef.current
    if (!contentElement) return

    const handleScroll = () => {
      const { scrollTop, scrollHeight, clientHeight } = contentElement
      // Load more when scrolled to 80% of the content
      if (scrollTop + clientHeight >= scrollHeight * 0.8) {
        loadMore()
      }
    }

    contentElement.addEventListener('scroll', handleScroll)
    return () => contentElement.removeEventListener('scroll', handleScroll)
  }, [loadMore])

  const handleItemClick = (videoViewId: number, videoTitle: string) => {
    onNavigateCollection(videoViewId, videoTitle)
  }

  const handleJumpClick = (e: React.MouseEvent, videoUrl: string) => {
    e.stopPropagation()
    chrome.tabs.create({ url: videoUrl })
  }

  const handleChannelClick = (e: React.MouseEvent, channelUrl: string) => {
    e.stopPropagation()
    chrome.tabs.create({ url: channelUrl })
  }

  return (
    <div className="history-container">
      <div className="history-content" ref={contentRef}>
        {/* Header */}
        <div className="popup-header">
          <div className="popup-header-content">
            <button className="back-button" onClick={onBack}>
              <FiChevronLeft size={16} />
            </button>
            <div className="history-title-container">
              <FiClock size={20} className="history-icon" />
              <h2 className="history-title">{t('loggedIn_browsingHistory')}</h2>
            </div>
          </div>
        </div>

        {/* Video List */}
        <div className="history-list">
          {isLoading && (
            <div className="history-loading">
              <div className="loading-spinner" />
              <span>{t('loggedIn_loading')}</span>
            </div>
          )}

          {error && (
            <div className="history-error">
              <span>{error}</span>
            </div>
          )}

          {!isLoading && !error && videoViews.length === 0 && (
            <div className="history-empty">
              <FiClock size={48} className="empty-icon" />
              <span>{t('history_empty')}</span>
            </div>
          )}

          {!isLoading &&
            !error &&
            videoViews.map((videoView) => {
              const metadata = videoView.video.video_metadata as {
                channelName?: string
                channelUrl?: string
              } | null
              return (
                <div
                  key={videoView.id}
                  className="history-item"
                  onClick={() => handleItemClick(videoView.id, videoView.video.video_title)}
                >
                  <div className="history-thumbnail">
                    <img
                      src={getYouTubeThumbnailUrl(videoView.video.video_id)}
                      alt={videoView.video.video_title}
                      onError={(e) => {
                        // Fallback to a placeholder if image fails
                        ;(e.target as HTMLImageElement).style.display = 'none'
                      }}
                    />
                  </div>
                  <div className="history-info">
                    <div className="history-details">
                      <p className="history-video-title">{videoView.video.video_title}</p>
                      {metadata?.channelName && (
                        <p
                          className={`history-channel ${metadata?.channelUrl ? 'clickable' : ''}`}
                          onClick={
                            metadata?.channelUrl
                              ? (e) => handleChannelClick(e, metadata.channelUrl!)
                              : undefined
                          }
                        >
                          {metadata.channelName}
                        </p>
                      )}
                      <div className="history-meta">
                        <span className="history-platform">{videoView.video.platform}</span>
                        <span className="history-time">
                          {formatRelativeTime(videoView.updated_at)}
                        </span>
                      </div>
                    </div>
                    <button
                      className="history-jump-btn"
                      onClick={(e) => handleJumpClick(e, videoView.video.video_url)}
                      aria-label="Open video"
                    >
                      <FiExternalLink size={14} />
                    </button>
                  </div>
                </div>
              )
            })}

          {/* Loading more indicator */}
          {isLoadingMore && (
            <div className="history-loading-more">
              <div className="loading-spinner small" />
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
