import { useCallback, useEffect, useRef, useState } from 'react'
import { FiBook, FiChevronLeft, FiPlay, FiSquare, FiTrash2 } from 'react-icons/fi'
import { t } from '../i18n'
import { VideoCollectionListResponse, VideoCollectionResponse } from '../tools/client'
import { callAPI } from '../tools/clientBridge'
import { CollectionBridge } from '../tools/collectionBridge'
import { MessageSource } from '../types/messages'
import './MyCollectionPopup.css'

interface MyCollectionPopupProps {
  onBack: () => void
  videoViewId?: number
  videoTitle?: string
}

const PAGE_SIZE = 20

export function MyCollectionPopup({ onBack, videoViewId, videoTitle }: MyCollectionPopupProps) {
  const [collections, setCollections] = useState<VideoCollectionResponse[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [isLoadingMore, setIsLoadingMore] = useState(false)
  const [hasMore, setHasMore] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [sourceLanguage, setSourceLanguage] = useState<string | null>(null)
  const [playingId, setPlayingId] = useState<string | null>(null)
  const [deletingId, setDeletingId] = useState<string | null>(null)
  const contentRef = useRef<HTMLDivElement>(null)
  const audioRef = useRef<HTMLAudioElement | null>(null)

  // Load source language from settings when filtering by video
  useEffect(() => {
    if (videoViewId) {
      chrome.storage.sync.get('targetLanguage').then((result) => {
        setSourceLanguage(result.targetLanguage || 'en')
      })
    }
  }, [videoViewId])

  // Load initial data
  useEffect(() => {
    // Wait for sourceLanguage if filtering by videoViewId
    if (videoViewId && !sourceLanguage) return

    const loadCollections = async () => {
      try {
        setIsLoading(true)
        setError(null)
        const response = await callAPI<VideoCollectionListResponse>('getVideoCollections', {
          skip: 0,
          limit: PAGE_SIZE,
          video_view_id: videoViewId,
          source_language: videoViewId ? sourceLanguage! : undefined,
        })
        setCollections(response.items)
        setHasMore(response.items.length < response.total)
      } catch (err) {
        console.error('Failed to load collections:', err)
        setError(err instanceof Error ? err.message : 'Failed to load collections')
      } finally {
        setIsLoading(false)
      }
    }

    loadCollections()
  }, [videoViewId, sourceLanguage])

  // Load more data
  const loadMore = useCallback(async () => {
    if (isLoadingMore || !hasMore) return

    try {
      setIsLoadingMore(true)
      const response = await callAPI<VideoCollectionListResponse>('getVideoCollections', {
        skip: collections.length,
        limit: PAGE_SIZE,
        video_view_id: videoViewId,
        source_language: videoViewId ? sourceLanguage! : undefined,
      })
      setCollections((prev) => [...prev, ...response.items])
      setHasMore(collections.length + response.items.length < response.total)
    } catch (err) {
      console.error('Failed to load more collections:', err)
    } finally {
      setIsLoadingMore(false)
    }
  }, [isLoadingMore, hasMore, collections.length, videoViewId, sourceLanguage])

  // Scroll event handler for infinite scroll
  useEffect(() => {
    const contentElement = contentRef.current
    if (!contentElement) return

    const handleScroll = () => {
      const { scrollTop, scrollHeight, clientHeight } = contentElement
      // Load more when scrolled to 80% of the content
      if (scrollTop + clientHeight >= scrollHeight * 0.8) {
        loadMore()
      }
    }

    contentElement.addEventListener('scroll', handleScroll)
    return () => contentElement.removeEventListener('scroll', handleScroll)
  }, [loadMore])

  // Cleanup audio on unmount
  useEffect(() => {
    return () => {
      if (audioRef.current) {
        audioRef.current.pause()
        audioRef.current = null
      }
    }
  }, [])

  const handlePlayAudio = (e: React.MouseEvent, collection: VideoCollectionResponse) => {
    e.stopPropagation()

    // If already playing this audio, stop it
    if (playingId === collection.id) {
      if (audioRef.current) {
        audioRef.current.pause()
        audioRef.current = null
      }
      setPlayingId(null)
      return
    }

    // Stop any currently playing audio
    if (audioRef.current) {
      audioRef.current.pause()
      audioRef.current = null
    }

    // Create and play new audio
    const audio = new Audio(collection.audio_base64)
    audioRef.current = audio
    setPlayingId(collection.id)

    audio.play().catch((err) => {
      console.error('Failed to play audio:', err)
      setPlayingId(null)
    })

    audio.onended = () => {
      setPlayingId(null)
      audioRef.current = null
    }
  }

  const handleDelete = async (e: React.MouseEvent, collectionId: string) => {
    e.stopPropagation()
    if (deletingId) return // 

    setDeletingId(collectionId)
    try {
      // Use CollectionBridge to delete via Background Service
      // This ensures CollectionManager cache is updated and broadcasts COLLECTION_REMOVED
      // to all YouTube tabs, so Sidebar can refresh highlights
      await CollectionBridge.removeCollection(collectionId, videoViewId, MessageSource.POPUP)
      setCollections((prev) => prev.filter((c) => c.id !== collectionId))
    } catch (err) {
      console.error('Failed to delete collection:', err)
    } finally {
      setDeletingId(null)
    }
  }

  const handleItemClick = (videoUrl: string) => {
    chrome.tabs.create({ url: videoUrl })
  }

  return (
    <div className="collection-container">
      <div className="collection-content" ref={contentRef}>
        {/* Header */}
        <div className="popup-header">
          <div className="popup-header-content">
            <button className="back-button" onClick={onBack}>
              <FiChevronLeft size={16} />
            </button>
            <div className="collection-title-container">
              {!videoViewId && <FiBook size={20} className="collection-icon" />}
              <h2 className="collection-title">{videoTitle || t('loggedIn_myCollection')}</h2>
            </div>
          </div>
        </div>

        {/* Collection List */}
        <div className="collection-list">
          {isLoading && (
            <div className="collection-loading">
              <div className="loading-spinner" />
              <span>{t('loggedIn_loading')}</span>
            </div>
          )}

          {error && (
            <div className="collection-error">
              <span>{error}</span>
            </div>
          )}

          {!isLoading && !error && collections.length === 0 && (
            <div className="collection-empty">
              <FiBook size={48} className="empty-icon" />
              <span>{t('collection_empty')}</span>
            </div>
          )}

          {!isLoading &&
            !error &&
            collections.map((collection) => (
              <div
                key={collection.id}
                className="collection-item"
                onClick={() => handleItemClick(collection.video.video_url)}
              >
                {/* Thumbnail */}
                <div className="collection-thumbnail">
                  <img
                    src={collection.image_base64}
                    alt={collection.word}
                    onError={(e) => {
                      ;(e.target as HTMLImageElement).style.display = 'none'
                    }}
                  />
                </div>

                {/* Content */}
                <div className="collection-item-content">
                  <div className="collection-text">
                    <p className="collection-word">{collection.word}</p>
                    <p className="collection-definition">{collection.definitions}</p>
                  </div>

                  {/* Action buttons */}
                  <div className="collection-actions">
                    <button
                      className="collection-action-btn"
                      onClick={(e) => handlePlayAudio(e, collection)}
                      aria-label={playingId === collection.id ? 'Stop' : 'Play'}
                    >
                      {playingId === collection.id ? <FiSquare size={16} /> : <FiPlay size={16} />}
                    </button>
                    <button
                      className={`collection-action-btn collection-delete-btn${deletingId === collection.id ? ' deleting' : ''}`}
                      onClick={(e) => handleDelete(e, collection.id)}
                      disabled={deletingId === collection.id}
                      aria-label="Delete"
                    >
                      <FiTrash2 size={16} />
                    </button>
                  </div>
                </div>
              </div>
            ))}

          {/* Loading more indicator */}
          {isLoadingMore && (
            <div className="collection-loading-more">
              <div className="loading-spinner small" />
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
