/**
 * SettingsBridge - Popup to Background Service settings communication layer
 *
 * All settings operations are proxied through Background Service,
 * which broadcasts changes to all Content Scripts.
 *
 * This ensures that when interface language changes in Popup,
 * all YouTube tabs receive the update and refresh their UI.
 */

import { MessageSource, MessageType, UserSettings, SettingsResponse } from '../types/messages'
import { sendMessageWithResponse } from './messageUtils'

// Default settings when none are stored
const DEFAULT_SETTINGS: UserSettings = {
  nativeLanguage: 'zh-CN',
  targetLanguage: 'en',
  interfaceLanguage: 'zh_CN',
}

export const SettingsBridge = {
  /**
   * Load current settings from Background Service
   *
   * @returns SettingsResponse with current settings
   */
  async loadSettings(): Promise<SettingsResponse> {
    try {
      const response = await sendMessageWithResponse<SettingsResponse>(
        MessageSource.POPUP,
        MessageType.SETTINGS_LOAD,
        {},
      )
      return response || { success: true, settings: DEFAULT_SETTINGS }
    } catch (error) {
      console.error('[SettingsBridge] loadSettings failed:', error)
      return { success: false, error: 'Failed to load settings' }
    }
  },

  /**
   * Save settings through Background Service
   *
   * Background will:
   * 1. Save to chrome.storage.sync
   * 2. Broadcast SETTINGS_CHANGED to all YouTube tabs
   *
   * @param settings - Partial settings to save (only changed keys)
   * @returns SettingsResponse with updated settings
   */
  async saveSettings(settings: Partial<UserSettings>): Promise<SettingsResponse> {
    try {
      const response = await sendMessageWithResponse<SettingsResponse>(
        MessageSource.POPUP,
        MessageType.SETTINGS_SAVE,
        { settings },
      )
      return response || { success: false, error: 'No response from Background' }
    } catch (error) {
      console.error('[SettingsBridge] saveSettings failed:', error)
      const errorMessage = error instanceof Error ? error.message : 'Failed to save settings'
      return { success: false, error: errorMessage }
    }
  },
}
