import React, { useEffect, useState } from 'react'
import ReactDOM from 'react-dom/client'
import { initI18n, t } from '../i18n'
import { UserResponse } from '../tools/client'
import { AuthBridge } from '../tools/authBridge'
import { AuthPopup } from './AuthPopup'
import './index.css'
import { HomePopup } from './HomePopup'
import { SettingsPopup } from './SettingsPopup'
import { BrowsingHistoryPopup } from './BrowsingHistoryPopup'
import { MyCollectionPopup } from './MyCollectionPopup'

type ViewType = 'main' | 'settings' | 'history' | 'collection'

interface VideoCollectionState {
  videoViewId: number
  videoTitle: string
}

function App() {
  const [isAuthenticated, setIsAuthenticated] = useState<boolean | null>(null)
  const [isI18nReady, setIsI18nReady] = useState(false)
  const [initialUser, setInitialUser] = useState<UserResponse | null>(null)
  const [currentView, setCurrentView] = useState<ViewType>('main')
  const [videoCollectionState, setVideoCollectionState] = useState<VideoCollectionState | null>(
    null,
  )

  useEffect(() => {
    const init = async () => {
      // Initialize i18n first
      await initI18n()
      setIsI18nReady(true)

      // Check auth state via Background Service (Single Source of Truth)
      const { authenticated, user } = await AuthBridge.checkAuth()

      // Set auth states
      setInitialUser(user || null)
      setIsAuthenticated(authenticated)
    }

    init()
  }, [])

  if (isAuthenticated === null || !isI18nReady) {
    return (
      <div
        style={{
          width: '350px',
          height: '520px',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          background: '#000000',
          color: '#ffffff',
        }}
      >
        {isI18nReady ? t('loggedIn_loading') : 'Loading...'}
      </div>
    )
  }

  // Callback when login succeeds - update state directly without reload
  const handleLoginSuccess = (user: UserResponse) => {
    setInitialUser(user)
    setIsAuthenticated(true)
  }

  // Callback when logout succeeds - update state directly without reload
  const handleLogout = () => {
    setInitialUser(null)
    setIsAuthenticated(false)
    setCurrentView('main')
  }

  // Not authenticated: show login page
  if (!isAuthenticated) {
    return <AuthPopup onLoginSuccess={handleLoginSuccess} />
  }

  // Authenticated: show main, settings, history, or collection view
  if (currentView === 'settings') {
    return <SettingsPopup onBack={() => setCurrentView('main')} />
  }

  if (currentView === 'history') {
    return (
      <BrowsingHistoryPopup
        onBack={() => setCurrentView('main')}
        onNavigateCollection={(videoViewId, videoTitle) => {
          setVideoCollectionState({ videoViewId, videoTitle })
          setCurrentView('collection')
        }}
      />
    )
  }

  if (currentView === 'collection') {
    const goBackToHistory = videoCollectionState !== null
    return (
      <MyCollectionPopup
        onBack={() => {
          setVideoCollectionState(null)
          setCurrentView(goBackToHistory ? 'history' : 'main')
        }}
        videoViewId={videoCollectionState?.videoViewId}
        videoTitle={videoCollectionState?.videoTitle}
      />
    )
  }

  return (
    <HomePopup
      initialUser={initialUser}
      onNavigateSettings={() => setCurrentView('settings')}
      onNavigateHistory={() => setCurrentView('history')}
      onNavigateCollection={() => setCurrentView('collection')}
      onLogout={handleLogout}
    />
  )
}

ReactDOM.createRoot(document.getElementById('app') as HTMLElement).render(<App />)
