import { useState, useEffect } from 'react'
import { FcGoogle } from 'react-icons/fc'
import { tBrowser } from '../i18n'
import { UserResponse } from '../tools/client'
import { AuthBridge } from '../tools/authBridge'
import './AuthPopup.css'

interface AuthPopupProps {
  onLoginSuccess: (user: UserResponse) => void
}

export function AuthPopup({ onLoginSuccess }: AuthPopupProps) {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [username, setUsername] = useState('')
  const [confirmPassword, setConfirmPassword] = useState('')
  const [activeTab, setActiveTab] = useState<'login' | 'register'>('login')
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  // Clear error when switching tabs
  useEffect(() => {
    setError(null)
  }, [activeTab])

  const validateEmail = (email: string): boolean => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    return emailRegex.test(email)
  }

  const handleSubmit = async (e: React.FormEvent, type: 'login' | 'register') => {
    e.preventDefault()
    setError(null)
    setIsLoading(true)

    // Frontend validation
    try {
      // Validate email format
      if (!validateEmail(email)) {
        setError('Invalid email format')
        setIsLoading(false)
        return
      }

      // Validate password length (8-12 characters)
      if (password.length < 8 || password.length > 12) {
        setError('Password must be 8-12 characters long')
        setIsLoading(false)
        return
      }

      if (type === 'register') {
        // Validate username length (max 12 characters)
        if (username && username.length > 12) {
          setError('Username must be at most 12 characters')
          setIsLoading(false)
          return
        }

        // Validate password confirmation
        if (password !== confirmPassword) {
          setError('Passwords do not match')
          setIsLoading(false)
          return
        }
      }
    } catch (validationError) {
      setIsLoading(false)
      return
    }

    try {
      if (type === 'register') {
        // Register user via Background Service
        const registerResult = await AuthBridge.register(email, password, username || undefined)
        if (!registerResult.success) {
          setError(registerResult.error || 'Registration failed')
          setIsLoading(false)
          return
        }

        // After successful registration, automatically login
        const loginResult = await AuthBridge.login(email, password)
        if (!loginResult.success) {
          setError(loginResult.error || 'Login failed after registration')
          setIsLoading(false)
          return
        }
      } else {
        // Login user via Background Service
        const loginResult = await AuthBridge.login(email, password)
        if (!loginResult.success) {
          setError(loginResult.error || 'Login failed')
          setIsLoading(false)
          return
        }
      }

      // Success - get user and notify parent
      const userResult = await AuthBridge.getUser()
      if (userResult.success && userResult.user) {
        onLoginSuccess(userResult.user)
      } else {
        // Fallback: reload if can't get user
        window.location.reload()
      }
    } catch (err) {
      // Handle unexpected error
      const errorMessage = err instanceof Error ? err.message : 'Unknown error'
      setError(errorMessage)
      console.error(`${type} error:`, err)
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="auth-popup-container">
      {/* Loading Overlay */}
      {isLoading && (
        <div className="auth-loading-overlay">
          <div className="auth-spinner"></div>
        </div>
      )}

      <div className="auth-popup-content">
        {/* Tabs Header */}
        <div className="auth-tabs">
          <button
            onClick={() => setActiveTab('login')}
            className={`auth-tab-button ${activeTab === 'login' ? 'active' : ''}`}
          >
            {tBrowser('auth_login_tab')}
          </button>
          <button
            onClick={() => setActiveTab('register')}
            className={`auth-tab-button ${activeTab === 'register' ? 'active' : ''}`}
          >
            {tBrowser('auth_register_tab')}
          </button>
        </div>

        {/* Login Form */}
        {activeTab === 'login' && (
          <form onSubmit={(e) => handleSubmit(e, 'login')} className="auth-form">
            {error && <div className="auth-error">{error}</div>}

            <div className="auth-form-group">
              <label htmlFor="login-email" className="auth-form-label">
                {tBrowser('auth_email_label')}
              </label>
              <input
                id="login-email"
                type="email"
                placeholder={tBrowser('auth_email_placeholder')}
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="auth-form-input"
                required
              />
            </div>

            <div className="auth-form-group">
              <label htmlFor="login-password" className="auth-form-label">
                {tBrowser('auth_password_label')}
              </label>
              <input
                id="login-password"
                type="password"
                placeholder={tBrowser('auth_password_placeholder')}
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="auth-form-input"
                minLength={8}
                maxLength={12}
                required
              />
            </div>

            <div className="auth-forgot-password">
              <button type="button">{tBrowser('auth_forgotPassword')}</button>
            </div>

            <button type="submit" className="auth-submit-button" disabled={isLoading}>
              {tBrowser('auth_login_button')}
            </button>

            {/* Divider */}
            <div className="auth-divider">
              <div className="auth-divider-line"></div>
              <div className="auth-divider-text">
                <span>{tBrowser('auth_or')}</span>
              </div>
            </div>

            {/* Social Login */}
            <button type="button" className="auth-google-button">
              <FcGoogle size={20} />
              {tBrowser('auth_googleLogin')}
            </button>
          </form>
        )}

        {/* Register Form */}
        {activeTab === 'register' && (
          <form
            onSubmit={(e) => handleSubmit(e, 'register')}
            className="auth-form auth-form-register"
          >
            {error && <div className="auth-error">{error}</div>}

            <div className="auth-form-group">
              <label htmlFor="register-username" className="auth-form-label">
                {tBrowser('auth_username_label')}
              </label>
              <input
                id="register-username"
                type="text"
                placeholder={tBrowser('auth_username_placeholder')}
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                className="auth-form-input"
                maxLength={12}
                required
              />
            </div>

            <div className="auth-form-group">
              <label htmlFor="register-email" className="auth-form-label">
                {tBrowser('auth_email_label')}
              </label>
              <input
                id="register-email"
                type="email"
                placeholder={tBrowser('auth_email_placeholder')}
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="auth-form-input"
                required
              />
            </div>

            <div className="auth-form-group">
              <label htmlFor="register-password" className="auth-form-label">
                {tBrowser('auth_password_label')}
              </label>
              <input
                id="register-password"
                type="password"
                placeholder={`${tBrowser('auth_password_placeholder')} (${tBrowser('auth_passwordHint')})`}
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="auth-form-input"
                minLength={8}
                maxLength={12}
                required
              />
            </div>

            <div className="auth-form-group">
              <label htmlFor="register-confirm-password" className="auth-form-label">
                {tBrowser('auth_confirmPassword_label')}
              </label>
              <input
                id="register-confirm-password"
                type="password"
                placeholder={tBrowser('auth_password_placeholder')}
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                className="auth-form-input"
                minLength={8}
                maxLength={12}
                required
              />
            </div>

            <button type="submit" className="auth-submit-button" disabled={isLoading}>
              {tBrowser('auth_register_button')}
            </button>
          </form>
        )}
      </div>
    </div>
  )
}
