/**
 * AuthBridge - Popup to Background Service authentication communication layer
 *
 * All authentication operations are proxied through Background Service,
 * which serves as the Single Source of Truth for token management.
 *
 * This eliminates race conditions between Popup and Background having
 * independent APIClient instances with unsynchronized token states.
 */

import { MessageSource, MessageType } from '../types/messages'
import { sendMessageWithResponse } from './messageUtils'
import { UserResponse } from './client'

export interface AuthResponse<T = void> {
  success: boolean
  error?: string
  user?: UserResponse
  data?: T
}

export const AuthBridge = {
  /**
   * Check current authentication status
   *
   * @returns Object containing authenticated status and optional user info
   */
  async checkAuth(): Promise<{ authenticated: boolean; user?: UserResponse }> {
    try {
      const response = await sendMessageWithResponse<{
        data: { authenticated: boolean; user?: UserResponse }
      }>(MessageSource.POPUP, MessageType.CHECK_AUTH)

      return {
        authenticated: response?.data?.authenticated || false,
        user: response?.data?.user,
      }
    } catch (error) {
      console.error('[AuthBridge] checkAuth failed:', error)
      return { authenticated: false }
    }
  },

  /**
   * Login with email and password
   *
   * @param email - User email
   * @param password - User password
   * @returns AuthResponse with user info on success
   */
  async login(email: string, password: string): Promise<AuthResponse<UserResponse>> {
    try {
      const response = await sendMessageWithResponse<AuthResponse<UserResponse>>(
        MessageSource.POPUP,
        MessageType.AUTH_LOGIN,
        { email, password },
      )
      return response
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Login failed'
      return { success: false, error: errorMessage }
    }
  },

  /**
   * Logout current user
   *
   * @returns AuthResponse indicating success or failure
   */
  async logout(): Promise<AuthResponse> {
    try {
      const response = await sendMessageWithResponse<AuthResponse>(
        MessageSource.POPUP,
        MessageType.AUTH_LOGOUT,
        {},
      )
      return response
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Logout failed'
      return { success: false, error: errorMessage }
    }
  },

  /**
   * Register a new user
   *
   * @param email - User email
   * @param password - User password
   * @param name - Optional username
   * @returns AuthResponse with user info on success
   */
  async register(
    email: string,
    password: string,
    name?: string,
  ): Promise<AuthResponse<UserResponse>> {
    try {
      const response = await sendMessageWithResponse<AuthResponse<UserResponse>>(
        MessageSource.POPUP,
        MessageType.AUTH_REGISTER,
        { email, password, name },
      )
      return response
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Registration failed'
      return { success: false, error: errorMessage }
    }
  },

  /**
   * Get current user information
   *
   * @returns AuthResponse with user info on success
   */
  async getUser(): Promise<AuthResponse<UserResponse>> {
    try {
      const response = await sendMessageWithResponse<AuthResponse<UserResponse>>(
        MessageSource.POPUP,
        MessageType.AUTH_GET_USER,
        {},
      )
      return response
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : 'Failed to get user'
      return { success: false, error: errorMessage }
    }
  },
}
