import { useEffect, useState } from 'react'
import { FiChevronDown, FiChevronLeft, FiSettings } from 'react-icons/fi'
import {
  SUPPORTED_LANGUAGES,
  SUPPORTED_LOCALES,
  getLanguageName,
  localeToLanguageCode,
} from '../context'
import { getCurrentLocale, setInterfaceLanguage, t } from '../i18n'
import { SettingsBridge } from '../tools/settingsBridge'
import './SettingsPopup.css'

interface SettingsPopupProps {
  onBack: () => void
}

export function SettingsPopup({ onBack }: SettingsPopupProps) {
  const [nativeLanguage, setNativeLanguage] = useState('zh-CN')
  const [targetLanguage, setTargetLanguage] = useState('en')
  const [interfaceLanguage, setInterfaceLanguageState] = useState('zh_CN')
  const [isSaving, setIsSaving] = useState(false)
  const [saveSuccess, setSaveSuccess] = useState(false)

  useEffect(() => {
    //  SettingsBridge 
    const loadSettings = async () => {
      const response = await SettingsBridge.loadSettings()
      if (response.success && response.settings) {
        setNativeLanguage(response.settings.nativeLanguage)
        setTargetLanguage(response.settings.targetLanguage)
        setInterfaceLanguageState(response.settings.interfaceLanguage)
      } else {
        // Fallback to current locale if load fails
        setInterfaceLanguageState(getCurrentLocale())
      }
    }

    loadSettings()
  }, [])

  const handleSave = async () => {
    try {
      setIsSaving(true)
      setSaveSuccess(false)

      const currentLocale = getCurrentLocale()
      const interfaceChanged = interfaceLanguage !== currentLocale

      //  SettingsBridge  YouTube 
      const response = await SettingsBridge.saveSettings({
        nativeLanguage,
        targetLanguage,
        interfaceLanguage,
      })

      if (!response.success) {
        console.error('Failed to save settings:', response.error)
        return
      }

      //  i18n 
      if (interfaceChanged) {
        await setInterfaceLanguage(interfaceLanguage)
        window.location.reload()
        return
      }

      // 
      setSaveSuccess(true)
      setTimeout(() => {
        setSaveSuccess(false)
      }, 2000)
    } catch (error) {
      console.error('Failed to save settings:', error)
    } finally {
      setIsSaving(false)
    }
  }

  return (
    <div className="settings-container">
      <div className="settings-content">
        {/* Header */}
        <div className="popup-header">
          <div className="popup-header-content">
            <button className="back-button" onClick={onBack}>
              <FiChevronLeft size={16} />
            </button>
            <div className="settings-title-container">
              <FiSettings size={20} className="settings-icon" />
              <h2 className="settings-title">{t('settings_title')}</h2>
            </div>
          </div>
        </div>

        {/* Settings Form */}
        <div className="settings-form">
          <div className="settings-form-content">
            {/* Native Language */}
            <div className="form-group">
              <label className="form-label">{t('settings_nativeLanguage')}</label>
              <div className="select-wrapper">
                <select
                  className="form-select"
                  value={nativeLanguage}
                  onChange={(e) => setNativeLanguage(e.target.value)}
                >
                  {SUPPORTED_LANGUAGES.map((code) => (
                    <option key={code} value={code}>
                      {getLanguageName(code)}
                    </option>
                  ))}
                </select>
                <FiChevronDown size={16} className="select-icon" />
              </div>
            </div>

            {/* Target Language */}
            <div className="form-group">
              <label className="form-label">{t('settings_targetLanguage')}</label>
              <div className="select-wrapper">
                <select
                  className="form-select"
                  value={targetLanguage}
                  onChange={(e) => setTargetLanguage(e.target.value)}
                >
                  {SUPPORTED_LANGUAGES.map((code) => (
                    <option key={code} value={code}>
                      {getLanguageName(code)}
                    </option>
                  ))}
                </select>
                <FiChevronDown size={16} className="select-icon" />
              </div>
            </div>

            {/* Interface Language */}
            <div className="form-group">
              <label className="form-label">{t('settings_interfaceLanguage')}</label>
              <div className="select-wrapper">
                <select
                  className="form-select"
                  value={interfaceLanguage}
                  onChange={(e) => setInterfaceLanguageState(e.target.value)}
                >
                  {SUPPORTED_LOCALES.map((locale) => (
                    <option key={locale} value={locale}>
                      {getLanguageName(localeToLanguageCode(locale))}
                    </option>
                  ))}
                </select>
                <FiChevronDown size={16} className="select-icon" />
              </div>
            </div>

            {/* Save Button */}
            <button
              className={`save-button ${isSaving ? 'saving' : ''} ${saveSuccess ? 'success' : ''}`}
              onClick={handleSave}
              disabled={isSaving}
            >
              {isSaving
                ? t('settings_saving')
                : saveSuccess
                  ? t('settings_saved')
                  : t('settings_save')}
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}
