import { useEffect, useState } from 'react'
import { FiBook, FiClock, FiLogOut, FiSettings, FiTool, FiUser } from 'react-icons/fi'
import { t } from '../i18n'
import { UserResponse } from '../tools/client'
import { AuthBridge } from '../tools/authBridge'
import './HomePopup.css'

interface HomePopupProps {
  initialUser: UserResponse | null
  onNavigateSettings: () => void
  onNavigateHistory: () => void
  onNavigateCollection: () => void
  onLogout: () => void
}

export function HomePopup({
  initialUser,
  onNavigateSettings,
  onNavigateHistory,
  onNavigateCollection,
  onLogout,
}: HomePopupProps) {
  const [user, setUser] = useState<UserResponse | null>(initialUser)
  const [error, setError] = useState<string | null>(null)
  const isDev = import.meta.env.MODE === 'development'

  useEffect(() => {
    // Revalidate user data via Background Service
    const revalidateUser = async () => {
      try {
        const result = await AuthBridge.getUser()
        if (result.success && result.user) {
          setUser(result.user)
          setError(null)
        } else if (!result.success) {
          setError(result.error || 'Failed to load user')
        }
      } catch (error) {
        console.error('Failed to revalidate user:', error)
        const errorMessage = error instanceof Error ? error.message : 'Failed to load user'
        setError(errorMessage)
      }
    }

    // Only revalidate if we're showing the UI (no need if we don't have initial user yet)
    if (initialUser) {
      revalidateUser()
    }
  }, [initialUser])

  const handleLogout = async () => {
    try {
      setError(null)
      const result = await AuthBridge.logout()
      if (!result.success) {
        setError(result.error || 'Logout failed')
        return
      }
      // Notify parent to update auth state
      onLogout()
    } catch (error) {
      console.error('Logout failed:', error)
      const errorMessage = error instanceof Error ? error.message : 'Logout failed'
      setError(errorMessage)
    }
  }

  const handleMyCollection = () => {
    onNavigateCollection()
  }

  const handleBrowsingHistory = () => {
    onNavigateHistory()
  }

  const handleSettings = () => {
    onNavigateSettings()
  }

  const handleDebug = () => {
    // Open debug page in new tab
    chrome.tabs.create({ url: chrome.runtime.getURL('debug.html') })
  }

  return (
    <div className="logged-in-container">
      <div className="logged-in-content">
        {/* User Profile Section */}
        <div className="popup-header">
          <div className="popup-header-content user-profile">
            <div className="user-avatar">
              <FiUser size={24} />
            </div>
            <div className="user-name">{user?.name || user?.email || t('loggedIn_testUser')}</div>
          </div>
        </div>

        {/* Error Display */}
        {error && <div className="logged-in-error">{error}</div>}

        {/* Menu Section */}
        <div className="menu-section">
          <div className="menu-items">
            <button className="menu-item" onClick={handleMyCollection}>
              <div className="menu-item-content">
                <FiBook size={20} className="menu-icon" />
                <span className="menu-text">{t('loggedIn_myCollection')}</span>
              </div>
              <span className="menu-arrow"></span>
            </button>

            <button className="menu-item" onClick={handleBrowsingHistory}>
              <div className="menu-item-content">
                <FiClock size={20} className="menu-icon" />
                <span className="menu-text">{t('loggedIn_browsingHistory')}</span>
              </div>
              <span className="menu-arrow"></span>
            </button>

            <button className="menu-item" onClick={handleSettings}>
              <div className="menu-item-content">
                <FiSettings size={20} className="menu-icon" />
                <span className="menu-text">{t('loggedIn_settings')}</span>
              </div>
              <span className="menu-arrow"></span>
            </button>

            <button className="menu-item" onClick={handleLogout}>
              <div className="menu-item-content">
                <FiLogOut size={20} className="menu-icon" />
                <span className="menu-text">{t('loggedIn_logout')}</span>
              </div>
              <span className="menu-arrow"></span>
            </button>

            {/* Debug Menu Item (Development Only) */}
            {isDev && (
              <button className="menu-item" onClick={handleDebug}>
                <div className="menu-item-content">
                  <FiTool size={20} className="menu-icon" />
                  <span className="menu-text">Debug</span>
                </div>
                <span className="menu-arrow"></span>
              </button>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}
